@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu:300i,400,400i,500,500i,700,700i&display=swap);
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-family: "Ubuntu", sans-serif;
  color: #323846; }

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 50rem;
  margin: auto; }

.header {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 2em;
  font-weight: bold;
  letter-spacing: 0.1em;
  color: #3f5d95;
  border-bottom: 1px solid gray; }

.content {
  flex: 1 0 auto; }

.footer {
  flex-shrink: 0;
  padding: 10px;
  text-align: center;
  margin-top: 20px;
  border-top: 1px solid #80808069; }

a {
  color: #3f5d95; }

.project {
  margin-bottom: 10px; }
  .project__name {
    text-transform: capitalize;
    font-size: 1.2em; }
  .project__link {
    margin: 0 5px;
    text-decoration: none;
    border-bottom: 1px solid gray; }
    .project__link:hover {
      background-color: #c3f7eb;
      padding: 5px 2px 0 2px;
      border-bottom-color: #323846; }

.main-input {
  width: 100%;
  height: 50px;
  font-size: 2rem; }

.task {
  display: flex;
  justify-content: space-between;
  border-bottom: 0.5px solid #efe9e9; }
  .task__text {
    width: 50%;
    padding: 5px; }
  .task__actions {
    display: flex;
    padding: 10px; }
    .task__actions button,
    .task__actions a {
      margin: 5px; }

.form-control {
  width: 100%;
  box-shadow: 1px 1px 2px #80808075;
  border-radius: 5px;
  border: 1px solid #8080808f;
  padding: 0px 5px;
  font-size: 1.5rem; }

input.form-control {
  min-height: 40px;
  font-weight: bold; }

textarea.form-control {
  min-height: 100px; }

.mb-10 {
  margin-bottom: 10px; }

.mb-5 {
  margin-bottom: 5px; }

.mt-10 {
  margin-top: 10px; }

.mt-5 {
  margin-top: 5px; }

.ml-10 {
  margin-left: 10px; }

.ml-5 {
  margin-left: 5px; }

.mr-10 {
  margin-right: 10px; }

.mr-5 {
  margin-right: 5px; }

.action-link {
  margin-right: 15px; }
  .action-link:hover {
    border-bottom: 2px solid #3f5d95;
    cursor: pointer;
    font-weight: bold; }

.action-block {
  border-bottom: 1px solid rgba(179, 173, 173, 0.616);
  margin-top: 20px;
  padding-bottom: 10px; }

